/* eslint-disable import/no-anonymous-default-export */
import firebase from "firebase/app";
import "firebase/database";
import "firebase/analytics";
import "firebase/storage";

const remote = {
  apiKey: process.env.APIKEY,
  authDomain: "conveyanceme.firebaseapp.com",
  databaseURL: "https://conveyanceme.firebaseio.com",
  projectId: "conveyanceme",
  storageBucket: "docs-conveyanceme",
  messagingSenderId: process.env.MEASUREMENTID,
  appId: process.env.APPID,
  measurementId: process.env.MESSAGINGSENDERID,
};

if (firebase.apps.length === 0) {
  firebase.initializeApp(remote);
}

export default firebase;