import { List as GrommetList, Box, Text } from "grommet";
import { FormCheckmark as Check } from "grommet-icons";

const List = ({ list }) => {
  return (
    <GrommetList
      data={list}
      children={(datum) => (
        <Box direction="row">
          <Box pad={{ top: "xxsmall" }}>
            <Check size="small" />
          </Box>
          <Box pad={{ left: "small" }}>
            <Text>{datum}</Text>
          </Box>
        </Box>
      )}
    />
  );
};

export default List;
