import firebase from "./firebase";

const db = firebase.database().ref("/");
const store = firebase.storage();

class api {
  saveForm(stage, email) {
    return db.push({ stage, email }).catch((e) => console.log(e));
  }
  saveFile(file) {
    return store
      .ref(file.name)
      .put(file)
      .catch((e) => console.log(e));
  }
}

export default new api();