const Main = () => {
  const [checked, setChecked] = useState(true);

  const panelText = (panel) => (
    <Box gap="medium">
      <Text>{panel.body}</Text>
    </Box>
  );

  const panelTable = (panel) => (
    <Box align="center" pad="medium">
      <DataTable
        columns={conveyancerDataColumns}
        data={panel.table}
        resizeable
      />
    </Box>
  );

  const panelLink = (panel) => (
    <Box pad={{ top: "small" }} direction="row">
      <Copy />
      <Anchor
        href={panel.link.url}
        target="_blank"
        label={panel.link.label}
        margin={{ left: "xxsmall" }}
      />
    </Box>
  );

  const render = (panel) => (
    <AccordionPanel
      key={panel.header}
      label={
        <Text
          margin={{ left: "xsmall", vertical: "small" }}
          size="large"
          weight={0}
        >
          {panel.header}
        </Text>
      }
    >
      <Box pad={{ left: "xsmall", bottom: "medium" }}>
        {panelText(panel)}
        {panel.list ? List(panel) : null}
        {panel.table ? panelTable(panel) : null}
        {panel.link ? panelLink(panel) : null}
      </Box>
    </AccordionPanel>
  );

  const panels = (data) => data.map((panel) => render(panel));

  const onChange = (event) => setChecked(event.target.checked);
  return (
    <Box pad={{ vertical: "xlarge", horizontal: "large" }}>
      <Heading margin={{ top: "medium" }} size="small">
        Your typical steps to {checked ? "buy" : "a sale"}
      </Heading>
      <Box pad={{ bottom: "medium" }}>
        <Text margin={{ left: "xsmall" }}>
          Conveyancing is an arcane and long process to transfer ownership but
          we can help your {checked ? "purchase" : "sale"}.
        </Text>
        <Box pad="small" align="end">
          <CheckBox
            label={checked ? "Buyer" : "Seller"}
            checked={checked}
            onChange={onChange}
            toggle
          />
        </Box>
      </Box>
      <Accordion margin={{ vertical: "small" }}>
        {panels(checked ? BUYER_PANELS : SELLER_PANELS)}
      </Accordion>
    </Box>
  );
};

export default Main;
