import { useEffect, useState } from "react";
import { Box, Stack, Meter, Text } from "grommet";

const Chart = ({ data }) => {
  const [dataToRender, setDataToRender] = useState(null);
  const [active, setActive] = useState(null);

  const stats = [
    {
      value: 46,
      onHover: () => setActive({ l: "changed mind", v: 46 }),
    },
    {
      value: 23,
      onHover: () => setActive({ l: "fail to renegotiate", v: 23 }),
    },
    {
      value: 12,
      onHover: () => setActive({ l: "lending shortfall", v: 12 }),
    },
    {
      value: 10,
      onHover: () => setActive({ l: "survey nightmare", v: 10 }),
    },
    {
      value: 8,
      onHover: () => setActive({ l: "collapsed chain", v: 8 }),
    },
  ];

  useEffect(() => {
    switch (data) {
      case "stats":
        setDataToRender(stats);
        break;
      default:
        break;
    }
      // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Box align="center" pad="medium">
      <Stack anchor="center">
        <Meter
          type="circle"
          values={dataToRender}
          size="small"
          thickness="medium"
          round
        />
        <Box align="center">
          <Box direction="row" align="center" pad={{ bottom: "xsmall" }}>
            <Text size="xxlarge" weight="bold">
              {active ? active.v : 38}
            </Text>
            <Text>%</Text>
          </Box>
          <Box direction="column" justify="center">
            <Text textAlign="center" size="small">
              {active ? active.l : "sales fail"}
            </Text>
          </Box>
        </Box>
      </Stack>
    </Box>
  );
};

export default Chart;
