import { Card as CardHolder, CardBody, CardFooter, Box, Text } from "grommet";

const Card = ({ title, subTitle, children, footer, size }) => {
  return (
    <CardHolder key={title} width={size}>
      <CardBody pad="small">
        <Box align="center" pad="small">
          <Box>
            <Text size="large" weight="bold">
              {title}
            </Text>
            <Text>{subTitle}</Text>
          </Box>
        </Box>
        <Box align="center" pad={{ right: "small" }}>
          {children}
        </Box>
      </CardBody>
      <CardFooter
        pad={{ horizontal: "medium", vertical: "small" }}
      >
        <Text size="xsmall">{footer ? footer : null}</Text>
      </CardFooter>
    </CardHolder>
  );
};

export default Card;
