import { useState } from "react";
import { Grommet, Box, Grid, ResponsiveContext } from "grommet";
import { grommet } from "grommet/themes";
import { deepMerge } from "grommet/utils";
import themeMods from "./themeMods";

import Header from "./Header";
import Main from "./Main";
import Side from "./Side";
import Footer from "./Footer";

const theme = deepMerge(grommet, themeMods);

const columns2 = ["65%", "35%"];
const columns = {
  small: ["auto"],
  medium: columns2,
  large: columns2,
  xlarge: columns2,
};

const areas3x3 = [
  ["header", "header"],
  ["main", "side"],
  ["footer", "footer"],
];
const areas = {
  small: [["header"], ["main"], ["side"], ["footer"]],
  medium: areas3x3,
  large: areas3x3,
  xlarge: areas3x3,
};

const flexRows = ["xsmall", "flex"];
const rows = {
  small: ["xsmall", "auto", "flex"],
  medium: flexRows,
  large: flexRows,
  xlarge: flexRows,
};

const Responsive = ({ children, ...props }) => (
  <ResponsiveContext.Consumer>
    {(size) => {
      return (
        <Grid
          {...props}
          fill
          areas={!areas ? undefined : areas[size]}
          columns={!columns ? size : columns[size]}
          rows={!rows ? size : rows[size]}
        >
          {children}
        </Grid>
      );
    }}
  </ResponsiveContext.Consumer>
);

const App = () => {
  const [darkMode, setDarkMode] = useState(true);
  const [link, linkTo] = useState(<Main />);
  return (
    <Grommet full theme={theme} themeMode={darkMode ? "dark" : "light"}>
      <Responsive columns={columns} rows={rows} gap="small" areas={areas}>
        <Box gridArea="header">
          <Header darkMode={darkMode} setMode={setDarkMode} link={linkTo} />
        </Box>

        <Box gridArea="main">{link}</Box>

        <Box gridArea="side">
          <Side />
        </Box>

        <Box gridArea="footer">
          <Footer />
        </Box>
      </Responsive>
    </Grommet>
  );
};

export default App;
