import { useState, useContext } from "react";
import {
  Box,
  Grid,
  Button,
  Form,
  FormField,
  MaskedInput,
  RadioButtonGroup,
  ThemeContext,
  ResponsiveContext,
} from "grommet";
import { grommet } from "grommet/themes";
import { deepMerge } from "grommet/utils";
import api from "./service";
import Chart from "./components/Chart";
import List from "./components/List";
import Card from "./components/Card";

const Side = () => {
  const [value, setValue] = useState({});
  const [submit, setSubmit] = useState(false);
  const [formFields, setFormFields] = useState([
    {
      isVisible: true,
      name: "stage",
      theme: deepMerge(grommet, {
        formField: {
          border: { position: "none" },
        },
      }),
    },
    {
      isVisible: false,
      name: "email",
      theme: deepMerge(grommet, {
        formField: {
          border: { position: "inner", side: "bottom" },
        },
      }),
    },
  ]);

  const emailMask = [
    { regexp: /^[\w\-_.]+$/, placeholder: "email" },
    { fixed: "@" },
    { regexp: /^[\w]+$/, placeholder: "address" },
    { fixed: "." },
    { regexp: /^[\w]+$/, placeholder: "com" },
  ];
  const buyerSteps = [
    "searching",
    "made an offer",
    "conveyancing",
    "exchanged",
  ];

  const onSubmit = ({ value }) => {
    const { stage, email } = value;
    const nextFormElement = formFields.filter(
      (f) => f.name !== Object.keys(value)[0]
    );
    if (formFields.length > 1) {
      nextFormElement[0].isVisible = true;
      return setFormFields(nextFormElement);
    }
    setSubmit(true);
    return api.saveForm(stage, email);
  };

  const FormFieldType = (name) => {
    switch (name) {
      case "stage":
        return <RadioButtonGroup name={name} options={buyerSteps} />;
      case "email":
        return <MaskedInput name={name} mask={emailMask} />;
      default:
        return null;
    }
  };

  const form = () => (
    <Form
      value={value}
      onChange={(nextValue) => setValue(nextValue)}
      onSubmit={onSubmit}
    >
      {formFields.map((f, i) =>
        f.isVisible ? (
          <ThemeContext.Extend key={i} value={f.theme}>
            <FormField name={f.name}>{FormFieldType(f.name)}</FormField>
          </ThemeContext.Extend>
        ) : null
      )}
      <Box justify="center" margin={{ top: "medium" }}>
        <Button type="submit" label="Submit" />
      </Box>
    </Form>
  );

  const features = [
    "document sharing",
    "progress tracking",
    "secure messaging",
  ];

  const size = useContext(ResponsiveContext);

  return (
    <>
      <Box pad={{ top: "xlarge" }} />
      <Grid
        gap="small"
        columns={
          size === "small"
            ? { count: "fit", size: "small" }
            : { count: "fill", size: "full" }
        }
        justify="center"
        responsive
      >
        <Card
          title="Selling property has pitfalls"
          subTitle=""
          children={<Chart data="stats" />}
          footer=""
          size="medium"
        />
        <Card
          title="Don't be 1 of the 38%"
          subTitle="Our app gives you better..."
          children={<List list={features} />}
          footer=""
          size="medium"
        />
        <Card
          title="What stage are you at?"
          subTitle=""
          children={!submit ? form() : <Chart data="stats" />}
          footer=""
          size="medium"
        />
      </Grid>
    </>
  );
};

export default Side;
